// @import '~antd/lib/style/themes/default.less';

.fixedHeader {
  &:global(.ant-layout-header) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    min-width: 1100px;
    display: flex;
    padding: 0;
    z-index: 999;
    transition: width 0.2s;
    background: #fdfdfd;
    color: #495060;
    font-size: 14px;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
  }

  .logo {
    font-size: 20px;
    float: left;
    margin: 0 22px;

    img {
      width: 120px;
    }
  }
}
